.logged-in header.sticky.is-stuck {
  top: 32px;
}

.sticky {
  z-index: 5;

  .et_fb_desktop_mode & {
    z-index: 0;
    position: relative !important;
  }
}

.tip-top-bar {
  background: $brand-blue;
  position: relative;
}

.tip-top-links {
  ul, li {
    display: inline-block;
    margin: 0;
    padding: 0;
    line-height: 1;
    list-style: none;
    vertical-align: middle;
    height: 32px;
  }

  a {
    display: block;
    line-height: 32px;
    color: $white;

    &:hover, &:active, &:focus {
      color: lighten($light-green, 5%);
    }
  }
}

.cta-buttons {
  a {
    text-transform: uppercase;
    font-size: 14px;
    font-weight: bold;
    padding: 0 30px;
    color: $white !important;
  }

  li:first-child {
    a {
      background: $light-green;

      &:hover, &:active, &:focus {
        background: darken($light-green, 10%);
      }
    }
  }

  li:nth-child(2) {
    a {
      background: $rose;

      &:hover, &:active, &:focus {
        background: darken($rose, 10%);
      }
    }
  }
}

.social-buttons {
  a {
    width: 32px;
    text-indent: 100px;
    overflow: hidden;
    position: relative;

    &::before {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      text-indent: 0;
      text-align: center;
    }
  }
}

.top-bar {
  position: relative;
  border-bottom: 3px solid $light-gray;

  .top-bar-left {
    width: auto;
  }
}

.title-bar {
  background: none;

  .menu-icon {
    float: right;
    margin-top: 15px;
    width: 25px;
    height: 20px;
  }
}

.menu-icon::after {
  background: #707372;
 box-shadow: 0 7px 0 #707372, 0 14px 0 #707372;
}

.stickycontainer {
  height: auto;
}



